<template>
<main>
    <ImageModal v-if="modalVisible" @close="modalVisible = false" :imagedata="modalData"/>
    <ListModal v-if="modalVisible1" @close1="modalVisible1 = false" :listdata="modalData1"/>
    <Header :header="content"/>
    <input type="text" placeholder="نام ست یا قطعه مدنظر را جستجو نمایید ..." v-model="filter" style="width:500px;display:block;margin:10px auto;font-family:yekan;"/>
    <div class="container list">
        <table class="table table-hover container" >
            <thead>
                <tr>
                <th scope="col">ردیف</th>
                <th scope="col">نام ست</th>
                <th scope="col">تعداد</th>
                <th scope="col">وضعیت</th>
                <th scope="col">تصویر</th>
                <th scope="col">محتویات ست</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(set,index) in filteredRows" :key="`parts-${index}`" :class="[setStatus(set) ? 'green':'red']">
                <th scope="row">{{index+1}}</th>
                <td>{{set.name}}</td>
                <td>{{set.num}}</td>
                <td>{{set.status}}</td>
                <td><img  src="../../assets/gallery.png" style="widtg:24px;height:24px;cursor:pointer;"  @click="openModal(set)"></td>
                <td><img  src="../../assets/share.png" style="widtg:24px;height:24px;cursor:pointer;"  @click="openModal1(set)"></td>
                </tr>
            </tbody>
        </table>
    </div>
 
</main>
</template>

<script>
import ImageModal from '../../components/ImageModal.vue'
import ListModal from '../../components/ListModal.vue'

import Header from '../../components/Header.vue'

export default {
    name:'SetsReport',
    components:{Header,ImageModal,ListModal},
    data() {
        return {
            modalVisible: false,
            modalVisible1: false,
            modalData: null,
            modalData1: null,
            content:'لیست ست ها',
            filter:'',
            sets:[
                {
                name:'ست جراحی لثه',
                num:12,
                status:'کامل',
                pic:"back",
                parts:['الکل','پد','چسب','تیغ','قیچی','چاقو','الکل','پد','چسب','تیغ','قیچی','چاقو']
                },
                {
                name:'ست  اورژانس',
                num:23,
                status:'ناقص',
                pic:'set',
                parts:['الکل','پد','چسب','تیغ','قیچی','7','6','5','4','3','2','1']
                },
                {
                name:'ست  بیمارستان',
                num:87,
                status:'ناقص',
                pic:'tp',
                parts:['الکل','پد','چسب','تیغ','قیچی','g','f','e','d','c','b','a']
                }
            ],
            setStatus:(data)=>{
            if (data.status==='کامل') {
                return true
            }else{
                return false
            }
        } 
        }
    },
    computed: {
         filteredRows() {
            return this.sets.filter(set => {
            const parts = set.parts.toString().toLowerCase();
            const name = set.name.toLowerCase();
            const searchTerm = this.filter.toLowerCase();

            return name.includes(searchTerm) ||
            parts.includes(searchTerm);
            });
         }
    },
    methods: {
        openModal(data) {
            this.modalData = data;
            this.modalVisible = true;
        },
        openModal1(data) {
            this.modalData1 = data;
            this.modalVisible1 = true;
        },
        
        
    },
    
      
      
   
    
}
</script>

<style lang="scss" scoped>
.list{
        max-height: 65vh;
        overflow-y: scroll;
        table{
            
            direction: rtl;
            background-color: rgb(255, 255, 255);
            border-radius: 2px;
            text-align: center;
    
                thead{
                    background-color: #082C68;
                    color: rgb(255, 255, 255);
                    
        
                }
                
                    
             .green{
                background-color: #CFFFD4;
            }
             .red{
                background-color: #FFDFDF;
            }
                    
                
                
        }
    }
</style>